<template>
    <div id="app">
        <h2>상품 위험 등급</h2>
        <gauge-chart :series='initGaugeChartSeries'></gauge-chart>
        <h2>보유상품 현황</h2>
        <pie-chart :series='initPieChartSeries'></pie-chart>
        <h2>과거 수익률</h2>
        <area-chart :series='initAreaChartSeries'></area-chart>
        <h2>과거 수익률</h2>
        <lineArea-chart :series='initLineAreaChartSeries'></lineArea-chart>
        <h2>시장 지수 현황</h2>
        <plotLine-chart :series='initPlotLineChartSeries'></plotLine-chart>
    </div>
</template>

<script>
    import Gauge from '@/components/Chart/Gauge';
    import PieChart from '@/components/Chart/PieChart';
    import AreaChart from '@/components/Chart/AreaChart';
    import LineAreaChart from '@/components/Chart/LineAreaChart';
    import PlotLineChart from '@/components/Chart/PlotLineChart';

    export default {
        name: 'ChartTest',
        components: {
            gaugeChart: Gauge,
            pieChart: PieChart,
            areaChart: AreaChart,
            lineAreaChart: LineAreaChart,
            plotLineChart: PlotLineChart,
        },
        data() {
            return {
                chartData: this.fetchData()
            }
        },
        methods: {
            fetchData() {
                let data = [{
                    name: '국내주식',
                    y: 15.0,
                    selected: true
                }, {
                    name: '국내채권',
                    y: 20.0,
                    selected: true
                }, {
                    name: '해외주식',
                    y: 15.0,
                    selected: true
                }, {
                    name: '해외채권',
                    y: 50.0,
                    selected: true
                }];
                return data;
            }
        },
        mounted: function () {
        },
        computed : {
            initGaugeChartSeries : function() {
                return [{
                    // name: 'gauge',
                    data: [90], // 10, 30, 50, 70, 90
                    dataLabels: {
                        enabled: false
                    }
                }];
            },
            initPieChartSeries : function() {
                return [{
                    innerSize: '30%',
                    data: this.chartData,
                    colorByPoint: true,
                    dataLabels: {
                        enabled: true,
                        color: '#fff',
                        connectorWidth: 0,
                        distance: -70,
                        formatter: function () {
                            return Math.round(this.percentage) + ' %';
                        }
                    },
                    marker: {
                        enabled: false
                    },
                }];
            },
            initAreaChartSeries : function() {
                return [{
                    type: 'area',
                    name: 'area chart',
                    data: [0, 1, 4, 4, 5, 2, 3, 7],
                    color: '#1d48db'
                }];
            },
            initLineAreaChartSeries : function() {
                return [{
                    name: '',
                    type: 'area',
                    yAxis: 1,
                    data: [49.9, 71.5, 106.4, 129.2, 144.0, 176.0, 135.6, 148.5, 216.4, 194.1, 95.6, 54.4],
                    tooltip: {
                        valueSuffix: ' mm'
                    }
                },
                {
                    name: '',
                    type: 'line',
                    data: [7.0, 6.9, 9.5, 14.5, 18.2, 21.5, 25.2, 26.5, 23.3, 18.3, 13.9, 9.6],
                    tooltip: {
                        valueSuffix: ' °C'
                    }
                }]
            },
            initPlotLineChartSeries : function() {
                return  [{
                    name: '',
                    data: [90, 110, 80, null, null, null],
                    color: 'red',

                    marker: {
                        enabled: false
                    },
                }, {
                    name: '',
                    data: [90, 80, 100, null, null, null],

                    marker: {
                        enabled: false
                    },
                }]
            }
        }
    };


</script>

<style scoped>
    h2 { padding: 50px 20px 20px; text-align: left; }
</style>
